<template>
  <div class="login">
    <div class="main">
      <Speak></Speak>
      <div class="content">
        <el-input placeholder="自己的手机号也背不出来吗" prefix-icon="el-icon-phone" v-model.trim="custom.phone" clearable></el-input>
        <div class="d-twoBlock"></div>
        <el-input placeholder="敢不敢填付款密码" prefix-icon="el-icon-s-grid" type="password" show-password v-model.trim="custom.password"
          clearable></el-input>
        <div class="d-twoBlock"></div>
        <div class="bottom display-flex">
          <el-button size="small" type="primary" @click="login">登录</el-button>
          <div class="forget display-flex">
            <span @click="goToRegister">账号都没有，玩啥呢</span>
            <span @click="dialogVisible = true">忘记密码</span>
          </div>
        </div>
      </div>

      <el-dialog title="给你一次机会" :visible.sync="dialogVisible" width="500px" :before-close="handleClose">
        <el-input placeholder="没想到吧,找回密码是要邮箱的" v-model.trim="email" prefix-icon="el-icon-chat-line-square"></el-input>
        <div class="d-twoBlock"></div>
        <el-input placeholder="填写新密码,别再忘记了" v-model.trim="new_password" prefix-icon="el-icon-unlock" show-password></el-input>
        <div class="d-twoBlock"></div>
        <el-input placeholder="去看看邮箱中的验证码" v-model.trim="verity_code" prefix-icon="el-icon-key"></el-input>

        <span slot="footer" class="dialog-footer">
          <el-button @click="getVerityCode" :disabled="disabled">{{verity_text}}</el-button>
          <el-button type="primary">修 改</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import ForgetPassword from '@/components/experiment/ForgetPassword'
  import Speak from '@/components/experiment/Speak'
  export default {
    name: 'Login',
    components: {
      Speak,
      ForgetPassword
    },
    data() {
      return {
        url: '/user',
        email: '',
        verity_code: '',
        new_password: '',
        disabled: false,
        verity_text: '验证码',
        dialogVisible: false,
        custom: {
          phone: '',
          password: ''
        }
      }
    },
    methods: {
      goToRegister: function() {
        this.$router.push("/register");
      },
      // 获取验证码
      getVerityCode: function() {
        this.disabled = true;
        this.verity_text = "倒计时";
      },
      // 忘记密码
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      // 登录
      login: function() {
        if ((/^1[3456789]\d{9}$/.test(this.custom.phone)) && this.custom.password != "") {
          this.$axios.post(this.url + '/login', this.custom)
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  message: res.data.message,
                  type: 'success'
                })

                // 填写用户名，展示控制菜单
                this.$store.commit('loginChangeName', res.data.body);
                // 存入 token
                this.$store.commit('setToken',res.data.token);

                // 跳转首页
                this.$router.push('/');
              } else {
                this.$message({
                  message: res.data.message,
                  type: 'warning'
                })
              }
            })
        } else {
          this.$message({
            message: '信息能不能填写正确',
            type: 'warning'
          })
        }
      }
    }
  }
</script>

<style scoped>
  .content {
    width: 60%;
    margin: 0 auto;
  }

  .bottom {
    justify-content: space-between;
  }

  .forget {
    align-self: flex-end;
  }

  .forget span {
    cursor: pointer;
    display: block;
    margin-left: 20px;
  }

  .forget span:nth-child(1):hover {
    color: #409EFF;
  }

  .forget span:nth-child(2):hover {
    color: #E6A23C;
  }
</style>



